/* pulsate from App.css */
.details-close {
  width: 50px;
  height: 50px;
  min-height: 50px;
  min-width: 50px;
  padding: 0;
  margin: 0 0 25px 0;
  cursor: pointer;
  background: #eee;
  outline: 0 !important;
  border: 0 transparent;
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgba(74, 146, 168, 0.5);
  transition: all .6s;
  -webkit-animation: pulsate 1.4s cubic-bezier(0.66, 0, 0, 1) 0.8s infinite;
  animation: pulsate 1.4s cubic-bezier(0.66, 0, 0, 1) 0.8s infinite;
  /* pulsate from App.css */
}

.details-close:hover {
  background: transparent;
  transition: all .1s;
  -webkit-animation: none;
  animation: none;
}

.details-close svg {
  position: relative;
  width: 100%;
  height: 100%;
}

.details-close:hover svg {
  fill: #4a92a8;
  transition: all .2s;
}

.details-grid {
  display: grid;
  height: 100%;
  -ms-grid-rows: fit-content(100%) 1fr;
  -ms-grid-columns: fit-content(100%) 1fr;
      grid-template: fit-content(100%) 1fr/fit-content(100%) 1fr;
  grid-auto-flow: row dense;
  grid-auto-columns: 1fr;
  grid-gap: 25px;
  padding: 25px;
  border: 0 #fff;
  border-radius: 0;
}

.details-side {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.details-side.left {
  grid-column: span 1;
  margin: 0 auto 2.2rem;
}

.details-side.right {
  -ms-grid-column: 2;
  -ms-grid-column-span: 1;
  grid-column: 2 / span 1;
}

.details-side.top {
  grid-row: span 1;
}

.details-side.bottom {
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  grid-row: 2 / span 1;
  margin: 0 auto 2.2rem;
}

.details-side.wrap {
  grid-row: span 2;
}

.details-side img {
  width: 400px;
}

@media (max-width: 1380px) {
  .details-side.left {
    grid-column: span 2;
  }
  .details-side.right {
    grid-column: span 2;
  }
  .details-side.top {
    grid-row: span 1;
  }
  .details-side.bottom {
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    grid-row: 2 / span 1;
  }
  .details-side.wrap {
    grid-row: span 2;
    justify-content: center;
    align-items: center;
  }
  .details-side img {
    padding: 20px 0;
  }
}

@media (max-width: 600px) {
  .details-side img {
    width: 100%;
    padding: 20px 0;
  }
}

@media (max-width: 400px) {
  .details-panel .details-title {
    font-size: 1.1em !important;
  }
}

